html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin-bottom: 10vh;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}

.player-count-square-wrapper {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 16px;
  justify-content: center;
  width: 100%;
  align-items: center;
}

.player-count-square {
  font-size: 24px;
  font-weight: bold;
  width: calc(100% / 6);
  max-width: 64px;
  height: 64px;
  border: 2px solid darkslategrey;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.player-count-square.selected {
  border: 2px solid blue;
}

.get-setup {
  width: 96px;
  height: 48px;
}

.map-type-selection {
  text-transform: capitalize;
}

.selection-square-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
  width: 100%;
}

.selection-square {
  font-size: 24px;
  font-weight: bold;
  border: 2px solid darkslategrey;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 16px;
  height: 64px;
}

.selection-square.selected {
  border: 2px solid blue;
}

.archipelagos-count-square {
  width: calc(100% / 5);
  max-width: 64px;
  padding: 0 16px;
}

.archipelago-settings-wrapper {
  display: flex;
  flex-direction: column;
}

@media (min-width: 1024px) {
  .archipelago-settings-wrapper {
    flex-direction: row;
  }
}

.archipelago-setting-wrapper {
  display: flex;
  flex-direction: column;
  border: 2px solid brown;
  margin: 0 12px;
  padding: 12px;
}

.archipelago-error-message {
  color: red;
  font-weight: bold;
}

.no-bullets {
  list-style: none;
}
